<template lang='pug'>
  .header
    .header__nav
      .header__logo(
        @click="$router.push('/')"
      )
        img(src="../assets/logo@2x.png")
      .header__nav-items.header__nav-items--desktop
        router-link.header__nav-item(to="/about") 關於緹囍
        router-link.header__nav-item(to="/marriage-ring") 結婚對戒
        router-link.router-link.header__nav-item(to="/engage-ring") 求婚戒
        router-link.router-link.header__nav-item(to="/customize") 私人訂製珠寶
        a.router-link.header__nav-item.header__nav-item--fb(href="https://www.facebook.com/tandcjewelry/" target="_blank")
          img(src="../assets/facebook@2x.png")
        a.router-link.header__nav-item.header__nav-item--fb(href="https://www.instagram.com/tandcjewelry/" target="_blank")
          img(src="../assets/instagram-logo@2x.png")
        router-link.router-link.header__nav-item(to="/customize" v-if="false") 中 | 英
      .header__nav-items.header__nav-items--mobile
        burger-menu
</template>
<script>
import BurgerMenu from '@/components/BurgerMenu.vue';

export default {
  name: 'main-header',
  components: {
    BurgerMenu,
  },
};
</script>

<style lang="scss">
@import '../scss/_mediaQuery.scss';
@import '../scss/_variable.scss';

.header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
  background-color: #fff;
  &__nav {
    display: flex;
    justify-content: space-between;
  }
  &__logo {
    cursor: pointer;
    margin-left: calc(100%/12);
    width: 11.8%;
    @include tablet-and-mobile-only {
      width: calc(100%/3);
    }
    margin-top: 15px;
    margin-bottom: 15px;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__nav-items {
    display: none;
    &--desktop {
      @include tablet-landscape-and-up {
        display: flex;
        align-items: center;
      }
    }
    &--mobile {
      @include tablet-and-mobile-only {
        display: flex;
        align-items: center;
        margin-right: 5vw;
      }
    }
  }
  &__nav-item {
    margin-right: 40px;
    font-size: 17px;
    color: #3e3a39;
    &--fb {
      img {
        width: 30px;
        height: 30px;
      }
    }
    &:link {
      text-decoration: none;
    }
    &:visited {
      text-decoration: none;
    }
    &:hover {
      text-decoration: none;
      color: $color-golden;
    }

    &:active {
      text-decoration: none;
    }
  }
  &__nav-icon {
      background: #333;
    display: block;
    height: 2px;
    position: relative;
    transition: background .2s ease-out;
    width: 18px;
    &::before, &::after {
      background: #333;
      content: '';
      display: block;
      height: 100%;
      position: absolute;
      transition: all .2s ease-out;
      width: 100%;
    }
    &::before {
      top: 5px;
    }
    &::after {
      top: -5px;
    }
  }
}
</style>
