<template lang='pug'>
  b-container.appointment(fluid)
    b-row
      b-col.appointment__desc-wrapper(cols="12" md="3" offset-md="1")
        img.appointment__img(src="../assets/logo-gold@2x.png")
        h2 預約會面
        p 請輸入您的詳細資料。<br>如有任何意見或提問，歡迎與我們聯絡。
      b-col.appointment__contact-form(cols="11" md="6" offset-md="1")
        b-row
          b-col.appointment__input.appointment__input--name(cols="6")
            input(type="text" placeholder="名稱*" v-model="name")
          b-col.appointment__input.appointment__input--phone(cols="6")
            input(type="phone" placeholder="聯絡電話*" v-model="phone")
          b-col.appointment__input.appointment__input--mail(cols="12")
            input(type="email" placeholder="電子郵件*" v-model="mail")
          b-col.appointment__input.appointment__input--message(cols="12")
            textarea(placeholder="留言*" v-model="message")
          b-col.appointment__send-button
            decorated-button(:color="'golden'" @click="handleClick")
</template>
<script>
import axios from 'axios';
import DecoratedButton from '@/components/DecoratedButton.vue';

export default {
  name: 'appointment',
  data() {
    return {
      name: '',
      phone: '',
      mail: '',
      message: '',
    };
  },
  components: {
    DecoratedButton,
  },
  methods: {
    handleClick() {
      const APP_URL = 'https://script.google.com/macros/s/AKfycbyIHivXr_jaWHkL28sY4ALji3kIUQ7qQsCBDa8bbZhXinbOJUyc/exec';
      axios.get(APP_URL, {
        params: {
          name: this.name,
          phone: this.phone,
          mail: this.mail,
          message: this.message,
        },
      }).then(() => this.clearForm());
    },
    clearForm() {
      this.name = '';
      this.phone = '';
      this.mail = '';
      this.message = '';
    },
  },
};
</script>

<style lang="scss">
@import '../scss/_mediaQuery.scss';
@import '../scss/_variable.scss';

$cool-grey: #909399;

.appointment {
  margin-top: 60px;
  &__desc-wrapper {
    margin-top: 70px;
  }
  &__img {
    width: 120px;
    height: 60px;
  }
  h2 {
    color: $color-golden;
    margin-top: 30px;
  }
  &__contact-form {
    @include tablet-and-mobile-only {
      margin: 0 auto;
    }
  }
  &__input {
    input, textarea {
      width: 100%;
      border: none;
      outline: none;
      font-size: 15px;
      line-height: 1.6;

      &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
        color: $cool-grey;
        font-size: 12px;
        opacity: 1; /* Firefox */
        padding: 10px;
      }

      &:-ms-input-placeholder { /* Internet Explorer 10-11 */
        color: $cool-grey;
        font-size: 15px;
        padding: 10px;
      }

      &::-ms-input-placeholder { /* Microsoft Edge */
        color: $cool-grey;
        font-size: 15px;
        padding: 10px;
      }
    }
    input {
      padding-top: 7.5px;
      padding-bottom: 7.5px;
      border-bottom: 1px solid #eeeeee;
      &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
        line-height: normal;
      }

      &:-ms-input-placeholder { /* Internet Explorer 10-11 */
        line-height: normal;
      }

      &::-ms-input-placeholder { /* Microsoft Edge */
        line-height: normal;
      }
      &::-webkit-input-placeholder {
        line-height: normal;
      }
    }
    textarea {
      height: 120px;
      border: 1px solid #eeeeee;
      padding: 10px;

      overflow: auto;
      outline: none;
      box-shadow: none;
      resize: none;
      -webkit-appearance: none;
    }
    &--mail, &--message {
      margin-top: 30px;
    }
  }
  &__send-button {
    margin-top: 30px;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 30px;
  }
}
</style>
