import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/views/Home.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/customize',
      name: 'customize',
      component: () => import('./views/Customization.vue'),
    },
    {
      path: '/about',
      name: 'about',
      component: () => import('./views/About.vue'),
    },
    {
      path: '/marriage-ring',
      name: 'MarriageRing',
      component: () => import('./views/MarriageRing.vue'),
    },
    {
      path: '/engage-ring',
      name: 'EngageRing',
      component: () => import('./views/EngageRing.vue'),
    },
    {
      path: '/marriage-story-ring',
      name: 'MarriageStoryRing',
      component: () => import('./views/MarriageStoryRing.vue'),
    },
    {
      path: '/marriage-brew-ring',
      name: 'MarriageBrewRing',
      component: () => import('./views/MarriageBrewRing.vue'),
    },
    {
      path: '/brew-ring-series/:type',
      name: 'BrewRingSeries',
      component: () => import('./views/BrewRingSeries/index.vue'),
    },
    {
      path: '/ring/:id',
      component: () => import('./views/Ring.vue'),
    },
    {
      path: '/brew-ring/:id',
      component: () => import('./views/BrewRing/index.vue'),
    },
    {
      path: '/',
      name: 'home',
      component: Home,
    },
  ],
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
});
