<template lang='pug'>
.decorated-button(:class="classObject")
  button(type="button" @click="$emit('click')") {{ desc }}
</template>
<script>

export default {
  name: 'DecoratedButton',
  props: ['color', 'buttonDescription'],
  components: {
  },
  computed: {
    classObject() {
      return this.color
        ? `decorated-button--${this.color}`
        : '';
    },
    desc() {
      return this.buttonDescription
        ? this.buttonDescription
        : 'SEND';
    },
  },
};
</script>

<style lang="scss">
@import '../scss/_variable.scss';
$golden: $color-golden;
$white: white;

@mixin button-border($border-color) {
  button {
    border: 1px solid $border-color;
    color: $border-color;
    &:after {
      border: 6px solid transparent;
      border-bottom-color: $border-color;
      border-right-color: $border-color;
    }
  }
}


.decorated-button {
  &--golden {
    @include button-border($golden);
  }
  &--white {
    @include button-border($white);
  }
  button {
    position: relative;
    display: block;
    width: 120px;
    height: 50px;
    outline: none;
    background: none;

    &:focus {
      outline:0;
    }

    &:after{
      position: absolute;
      bottom: 0;
      height: 0;
      width: 0;
      right:0;
      content: "";
    }
  }
}
</style>
