<template lang='pug'>
  .burger(@click="isToggled = !isToggled")
    .burger__menu-toggle(:class="{'burger__menu-toggle--toggled': isToggled }")
      span
      span
      span
      ul.burger__menu-elem
        router-link(to="/about")
