        router-link(to="/marriage-ring")
          li 結婚對戒
        router-link(to="/engage-ring")
          li 求婚戒
        router-link(to="/customize")
          li 私人訂製珠寶
        a(href="https://www.facebook.com/tandcjewelry/" target="_blank")
          li Facebook
        a(href="https://www.instagram.com/tandcjewelry/" target="_blank")
          li Instagram
</template>
<script>
export default {
  name: 'burger-menu',
  data() {
    return {
      isToggled: false,
    };
  },
  components: {},
};
</script>

<style lang="scss">
@import '../scss/_mediaQuery.scss';
@import '../scss/_variable.scss';

.burger {
  &__menu-toggle {
    display: block;
    position: relative;
    top: 0;
    left: 0;
    z-index: 1;
    -webkit-user-select: none;
    user-select: none;
    span {
      display: block;
      width: 25px;
      height: 2px;
      z-index: 1;
      border-radius: 4px;
      position: relative;
      background: black;
      margin-bottom: 7px;
      transform-origin: 4px 0px;
      transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                  background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                  opacity 0.55s ease;
      &:first-child {
        transform-origin: 0% 0%;
        transition: width, 0.5s;
      }
      &:nth-last-child(2) {
        transform-origin: 0% 100%;
        transition: width, 0.5s;
      }
    }
    &--toggled {
      ul {
        transform: translate(-100%, 0%);
        opacity: 1;
      }
      span {
        opacity: 1;
        transform: rotate(45deg) translate(0px, 0px);
        background: #232323;
        width: 25px;
        transition: width, 0.5s;
        &:nth-last-child(3) {
          opacity: 0;
          transform: rotate(0deg) scale(0.2, 0.2);
        }
        &:nth-last-child(2) {
          opacity: 1;
          transform: rotate(-45deg) translate(0, 0px);
        }
      }
    }
  }
  &__menu-elem {
    position: absolute;
    width: 60vw;
    height: 100vh;
    top: 0;
    left: 72px;;
    padding-top: 125px;
    background: #fff;
    list-style-type: none;
    transform: translate(100%, 0%);
    transform-origin: 0% 0%;
    transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
    li {
      padding: 10px 0;
      font-size: 17px;
      color: #3e3a39;
    }
  }
}

</style>
