<template lang='pug'>
.image-block(:class="{ 'image-block--higher': moreHeigher }")
  .image-block__background(
    v-bind:style="{backgroundImage: 'url(' + imageSrc + ')'}"
    )
  .image-block__content
    h3(v-if="displayTitle") {{title}}
    .image-block__button(v-if="showButton")
      decorated-button(
        :color="'white'"
        @click="$emit('click')"
        :buttonDescription="'MORE'")
  .image-block__mask(v-if="supportMask"
    @mouseover="mouseenterHandler()"
    @mouseleave="mouseoutHandler()"
    )
    .image-block__mask-content
      .image-block__title
        h3 {{ title }}
      .image-block__description(v-if="maskDescription")
        p {{ maskDescription }}
      .image-block__button
        decorated-button(
          :color="'white'"
          @click="$emit('click')"
          :buttonDescription="'MORE'")

</template>
<script>
import DecoratedButton from '@/components/DecoratedButton.vue';

export default {
  name: 'ImageBlock',
  props: {
    imagePath: {
      type: String,
    },
    title: {
      type: String,
    },
    buttonDescription: {
      type: String,
    },
    showDescription: {
      default: true,
    },
    showButton: {
      default: true,
    },
    supportMask: {
      default: false,
    },
    maskDescription: {
      type: String,
      default: null,
    },
    moreHeigher: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      displayTitle: true,
    };
  },
  methods: {
    mouseenterHandler() {
      if (this.supportMask) {
        this.displayTitle = false;
      }
    },
    mouseoutHandler() {
      if (this.supportMask) {
        this.displayTitle = true;
      }
    },
  },
  components: {
    DecoratedButton,
  },
  computed: {
    imageSrc() {
      if (this.imagePath) {
        return require(`assets/${this.imagePath}`); // eslint-disable-line 
      }
      return '';
    },
  },
};
</script>

<style lang="scss">
.image-block {
  position: relative;
  width: 100%;
  height: 400px;
  overflow: hidden;
  &--higher {
    height: 600px;
  }
  &:hover, &:focus {
    .image-block__background {
      transform: scale(1.2);
    }
  }
  &__background {
    transition: all .5s;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
  }
  h3 {
    color: white;
    font-size: 30px;
  }
  &__content {
    position: absolute;
    top: 75%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &__button {
    margin-top: 30px;
    display: flex;
    justify-content: center;
  }
  &__mask {
    &:hover {
      opacity: 1;
      background-color:rgba(0,0,0,0.7);
      transform: translateY(0);
    }
    transform: translateY(10px);
    opacity: 0;
    transition: all 0.3s ease-in-out;
    position: absolute;
    top: 0;
    width: 100%;
    height: 400px;
    overflow: hidden;
  }
  &__mask-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &__description {
    color: white;
    width: 100%;
    margin: 0 auto;
    margin-top: 20px;
  }
}
</style>
